(function() {
  angular.module('homemademessClient', ['ngCookies', 'ngTouch', 'ngSanitize', 'ngAria', 'ngResource', 'ui.router', 'akoenig.deckgrid', 'ngMaterial', 'ngDropzone', 'ngTagsInput', 'ngLodash']);

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').controller('NavbarCtrl', function($scope, $resource, lodash, $q, $location, apiUrl) {
    var Auto, Images;
    $scope.tags = [];
    $scope.isActive = function(route) {
      return route === $location.path();
    };
    $scope.mapTags = function(tags) {
      return lodash.uniq(lodash.map(tags, '_id'));
    };
    Images = $resource(apiUrl + '/images');
    $scope.redoSearch = function() {
      return Images.get({
        tags: $scope.mapTags($scope.tags)
      }, function(result) {
        return $scope.view.images = result.images;
      });
    };
    Auto = $resource(apiUrl + '/auto');
    return $scope.findTags = function(value) {
      return Auto.query({
        q: value
      }).$promise;
    };
  });

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').controller('MainCtrl', function($scope, $http, apiUrl, $mdDialog, $document) {
    $scope.view = {};
    $scope.slide = {};
    $http.get(apiUrl + '/images').success(function(result) {
      $scope.view.images = result.images;
      $scope.view.tags = result.tags;
      return $scope.view.offset = 0;
    });
    $scope.keydown = function(e) {
      var i;
      console.log(e);
      if (e.keyCode) {
        i = $scope.slide.$index;
        switch (e.keyCode) {
          case 37:
            if (i > 0) {
              $scope.slide = $scope.view.images[i - 1];
            }
            break;
          case 39:
            if (i < $scope.view.images.length - 1) {
              $scope.slide = $scope.view.images[i + 1];
            }
            break;
          case 33:
            if (i !== 0) {
              $scope.slide = $scope.view.images[0];
            }
            break;
          case 34:
            if (i !== $scope.view.images.length - 1) {
              $scope.slide = $scope.view.images[$scope.view.images.length - 1];
            }
        }
        return $scope.$apply();
      }
    };
    return $scope.showSlide = function(slide) {
      if (window.innerWidth > 459) {
        $scope.slide = $scope.view.images[slide];
        return $mdDialog.show({
          clickOutsideToClose: true,
          scope: $scope,
          preserveScope: true,
          template: '<md-dialog aria-label="{{slide.filename}}" class="slide" ng-class="getOrientation()">' + '  <md-dialog-content>' + '     <img src="{{slide.derivative[2].uri}}" class="img-responsive" />' + '  </md-dialog-content>' + '</md-dialog>',
          controller: function($scope, $mdDialog, $document) {
            $document.on('keydown', $scope.keydown);
            $scope.lastpress = 0;
            return $scope.getOrientation = function() {
              var h, w;
              h = $scope.slide.derivative[2].height;
              w = $scope.slide.derivative[2].width;
              if (h > w) {
                return 'portrait';
              } else {
                return 'landscape';
              }
            };
          }
        });
      }
    };
  });

}).call(this);


(function() {
  angular.module('homemademessClient').run(function($log) {
    'ngInject';
    return $log.debug('runBlock end');
  });

}).call(this);


(function() {
  angular.module('homemademessClient').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $stateProvider.state('home', {
      url: '/',
      templateUrl: 'app/main/main.html',
      controller: 'MainCtrl',
      controllerAs: 'main'
    });
    return $urlRouterProvider.otherwise('/');
  });

}).call(this);


(function() {
  angular.module('homemademessClient').constant('apiUrl', 'http://api.homemademess.com');

}).call(this);


(function() {
  angular.module('homemademessClient').config(function($logProvider) {
    'ngInject';
    return $logProvider.debugEnabled(true);
  });

}).call(this);


